/* bouton burger */
.burger_icon {
    position: absolute;
    right: 1.5em;
    top: 1.4em;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 2.2em;
    width: 2.2em;
    /*padding: .6em;*/
    border: 2px solid var(--tertiary-color);
    border-radius: 1.1em;
    box-sizing: border-box;
    overflow: hidden;
    font-size: 1.6rem;
    color: var(--primary-color);
    background-color: var(--tertiary-color);
    transition: color .5s, background-color .5s, width .5s, content .5s;
}

.burger_icon:hover {
    cursor: pointer;   
    /*justify-content: right;*/
    width: 9em;
    color: var(--tertiary-color);
    background-color: white; 
}

.burger_icon_text {
    color: rgba(47, 74, 122, 0);
    transition: color 3s;
}

.burger_icon:hover .burger_icon_text {
    /*position: absolute;
    right: 1.8em;
    padding: .6em;*/
    color: var(--tertiary-color);
}

.burger_icon:focus {
    visibility: hidden;
}

/* l'image dans le bouton burger */
.burger_icon_fawesome {
    height: 1.6rem;
    width: 1.6rem;
}

/* Position and sizing of clickable cross button */
.burger_cross_btn {
    position: absolute;
    top: .7em;
    left: .7em;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: .5em;
    border: none;
    border-radius: 50%;
    background-color: inherit;
    transition: background-color .6s;
}
.burger_cross_btn:hover {
    cursor: pointer;
    background-color: #cfd4e9;
}
/* L'image du bouton croix */
.burger_cross {
    height: 2rem;
    width: 2rem;
}

/* le containeur du menu burger */
.burger_menu {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 101;
}

/* le containeur des liens dans le menu burger (caché par défaut) */
.burger_items_ctn {
    position: fixed;
    right: -200px;
    display: flex;
    flex-direction: column;
    /*align-items: left;*/
    height: 100%;
    width: 200px;
    padding: 4em 1.7em 0;
    font-size: 1.5rem;
    background-color: #b8bfda;
    visibility: hidden;
    transition: all 1s ease-out;
    z-index: 200;
}

.show_burger_menu .burger_items_ctn {
    right: 0;
    visibility: visible;
    transition: all 1s ease-out;
}

/* Individual item */
.burger_item {
    display: inline-block;
    margin-top: 1em;
    padding: .5em;
    border-radius: .4em;
    font-weight: 500;
    color: black;
}
.burger_item:hover {
    cursor: pointer;
    background-color: #cfd4e9;
}

.burger_item_signout {
    position: absolute;
    bottom: 2em;
    align-self: center;
    padding: .4em .8em;
    border-radius: .4em;
    color: var(--primary-color);
    background-color: var(--darkgrey-color);
    text-align: center;
}

.burger_items_ctn > hr {
    margin-top: 1.5em;
    margin-bottom: .8em;
    margin-left: .5em;
    border: 1px solid var(--grey-color);
}

/* Afficher et cacher des éléments */
.show {
    visibility: visible;
    opacity: 1;
    transition: all 2s;
}
.hide {
    visibility: hidden;
    opacity: 0;
    transition: all .5s;
}

/******************************************************************/
/* ---------------------- VERSION TABLETTE ---------------------- */
/******************************************************************/
@media screen and (min-width: 768px) {
    .burger_icon {
        top: .8em;
        right: 3em;
    }
}


