/******************************************************************/
/* ----------------------- VERSION MOBILE ----------------------- */
/******************************************************************/
.user_db_main {
    margin-top: 1em;
    padding-bottom: 3em;
    text-align: center;
}

.user_db_main > h2 {
    text-transform: uppercase;
}

.user_db_account_form,
.db_create_form {
    display: flex;
    flex-direction: column;
    max-width: 500px;
    margin: 1em auto; 
    padding: 1.5em;
    font-size: 1.4rem;
    background-color: var(--primary-color);
}
.db_create_form {
    max-width: unset;
}

.db_create_form > label {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    margin-bottom: 1em;
}

.db_create_form > label > span {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    margin-top: .2em;
    margin-right: .5em;
}

.db_create_form > label > input,
.db_create_form > label > textarea,
.db_create_form > label > select {
    display: block;
    width: 60%;
    margin-left: .5em;
    margin-right: 0;
    padding: .1em .4em;
}

.db_create_form > label > textarea {
    height: 11em;
}

.user_db_label {
    display: flex;
    align-items: center;
    margin-bottom: 1em;
    /*padding: .5em 1em;*/
    border: none;
    /*background-color: antiquewhite;*/
}

/*** les textes ***/
.user_db_label > *:first-child {
    display: inline-block;
    width: 42%;
    padding-right: .5em;
    text-align: right;
}
/*** les saisies ***/
.user_db_label > *:nth-child(2) {
    display: inline-block;
    width: 50%;
    padding: .2em .5em;
    font-size: 1.6rem;
    font-weight: 500;
}
/*** les boutons Modif ***/
.user_db_label > *:nth-child(3) {
    display: inline-block;
    align-items: center;
    justify-content: center;
    height: 1.5em;
    width: 1.5em;
    margin-left: .5em;
    border: none;
    background-color: var(--tertiary-color);
}

.email {
    border: 1px solid var(--verylightgrey-color);
    border-radius: .4em;
    text-align: left;
    background-color: var(--verylightgrey-color);
}

.modify_icon {
    font-size: .8em;
    color: white;
}

.db_create_form > button,
.user_db_account_form > button {
    display: block;
    margin: 0 auto;
    padding: .5em .7em .4em;
    border: none;
    border-radius: .4em;
    font-weight: 500;
    letter-spacing: 1.1;
    text-transform: uppercase;
    color: white;
    background-color: var(--tertiary-color);
}

.db_create_form > button:hover,
.user_db_account_form > button:hover {
    cursor: pointer;
}

.db_create_form > button {
    margin-top: 1em;
}

.msg {
    color: var(--highlight-color);
}

.msg_instructions {
    margin-bottom: 1em;
    text-align: justify;
    color: var(--highlight-color);
}

/******************************************************************/
/* ---------------------- VERSION TABLETTE ---------------------- */
/******************************************************************/
@media screen and (min-width: 768px) {
    .user_db_main {
        display: inline-block;
        margin-top: 0;
        padding: 80px auto;
    }

    .user_db_account_form {
        max-width: 600px;
    }

    .db_create_form {
        margin: 1em 4em 2em 4em;
    }
}

/******************************************************************/
/* ----------------------- VERSION DESKTOP ---------------------- */
/******************************************************************/
@media screen and (min-width: 1100px) {
    .db_create_form {
        width: 1000px;
        margin-left: auto;
        margin-right: auto;
    }
}